import { precompileTemplate } from "@ember/template-compilation";
export default precompileTemplate("{{#each this.breadCrumbs.items as |item|}}\n  {{#if (has-block)}}\n\n    {{yield (hash\n      component=(if item.route\n        (component \"bread-crumbs/link\" item=item)\n        (component \"bread-crumbs/text\" item=item)\n      )\n      model=item\n    )}}\n\n  {{else}}\n\n    {{#if item.route}}\n      <BreadCrumbs::Link @item={{item}} />\n    {{else}}\n      <BreadCrumbs::Text @item={{item}} />\n    {{/if}}\n\n  {{/if}}\n\n{{/each}}")